import { useEffect } from 'react'
import firebase from 'firebase/app'
import 'firebase/firestore'
import 'firebase/database'

const ipify = 'https://api.ipify.org?format=json'
const geoIpLookup = 'https://json.geoiplookup.io/'

function writeUserData(data) {
  firebase.firestore().collection("traffic").doc(`${Date.now()}`).set(data)
    .then(function () {
    })
    .catch(function (error) {
    });
}

const useTracker = () => {
  var firebaseConfig = {
    apiKey: "AIzaSyC5ZLDEMxbEhSPx6Bfsr9UKfS7f_Jb2qEA",
    authDomain: "tracker-a3fe7.firebaseapp.com",
    databaseURL: "https://tracker-a3fe7.firebaseio.com",
    projectId: "tracker-a3fe7",
    storageBucket: "tracker-a3fe7.appspot.com",
    messagingSenderId: "176214838229",
    appId: "1:176214838229:web:9c97169436dd03d3eb8b5c"
  };
  if (!firebase.apps.length) {
    firebase.initializeApp(firebaseConfig);
  }


  useEffect(() => {
    const getIp = async () => {
      const ipResponse = await fetch(ipify)
      const { ip } = await ipResponse.json()
      const geoResponse = await fetch(`${geoIpLookup}${ip}`)
      const result = await geoResponse.json()
      writeUserData(result)
    }
    getIp()
  }, [])
}

export default useTracker